<template>
  <div class="wrapper" ref="home">
    <StaticPart/>
    <Nearby/>
  </div>
</template>

<script>
import StaticPart from '@/views/home/StaticPart.vue'
import Nearby from '@/views/home/Nearby.vue'
import { onActivated, ref } from 'vue'
import { onBeforeRouteLeave } from 'vue-router'

export default {
  name: 'Home',
  components: {
    StaticPart,
    Nearby
  },
  setup () {
    const home = ref(null)
    const homeScroll = ref(0)

    // 记录滚动条位置
    onActivated(() => {
      home.value.scrollTop = homeScroll.value
      homeScroll.value = 0
    })
    onBeforeRouteLeave(() => {
      homeScroll.value = home.value.scrollTop
    })
    return {
      home
    }
  }
}
</script>

<style lang="scss" scoped>
.wrapper {
  position: absolute;
  top: 0;
  bottom: .5rem;
  left: 0;
  right: 0;
  padding: 0 .18rem .18rem .18rem;
  overflow: auto;
}
</style>
